.form-control
{display: inline-block; width: auto;}

Form.Label{
  display: inline-block;
        width: 110px;
}

form{
  width: 50%;
  white-space: normal;
  word-wrap: break-word;
}
.page {
  margin: 2%;
  display: flex;
  width: 50%;
  flex-direction: column;
}
body {
  width: 91%;
  margin: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
 
.box {
  width: 100%;
  height: 100;
  border: 1px solid #ddd;
}
.box .steps {
  
  border-bottom: 1px solid #ddd;
}
.box .steps ul {
  width: 100%;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.box .steps ul li div {
  color: #999;
  padding: 10px 0 15px 45px;
  position: relative;
  background: #f5f5f5;
  width: 165px;
}
.box .steps ul li div span {
  font-size: 13px;
}
.box .steps ul li:first-child div {
  
  width: 135px;
  padding-left: 15px;
}
.box .steps ul li div::before {
  
  content: " ";
  border-top: 50px solid transparent;
  border-bottom: 50px solid transparent;
  border-left: 30px solid #ddd;
  position: absolute;
  top: 50%;
  margin-top: -50px;
  left: 100%;
  z-index: 1;
  margin-left: 1px;
}
.box .steps ul li div::after {
  
  content: " ";
  border-top: 50px solid transparent;
  border-bottom: 50px solid transparent;
  border-left: 30px solid #f5f5f5;
  position: absolute;
  top: 50%;
  margin-top: -50px;
  left: 100%;
  z-index: 2;
}
.box .steps ul li.done div {
  border-color: #20a8d8 !important;
  color: #fff !important;
  background: #20a8d8 !important;
}
.box .steps ul li.done div::after {
  border-left: 30px solid #20a8d8;
}
.box .steps ul li.active div {
  border-color: #167495 !important;
  color: #fff !important;
  background: #167495 !important;
}
.box .steps ul li.active div::after {
  border-left: 30px solid #167495;
}
 
.box .step-component {
  padding: 20px;
  margin-left: 30%;
  height: 100%;
  width: 80%;
  /* display: flex; */
  align-items: center;
  justify-content: center;
}
.box .btn-component {
    width: 110%;
  justify-content: space-around;
  padding: 20px;
  display: flex;
}